import { useContext } from "react";
import { AppContext } from "../App"
import { Col } from "./column";

export function Table(props) {
  const { state, dispatch } = useContext(AppContext);

  return (
    <div className="table">
      <Col width="100" part="0" col="0" title="Ключевые фразы" />
      <Col width="100" part="0" col="1" title="Группы ключевых слов" />
      <Col width="100" part="0" col="2" title="Название компаний" />
      <Col width="50" part="0" col="3" title="YW" />
      <Col width="50" part="0" col="4" title='"YW"' />
    </div>
  )
}