import React, { useContext, useState } from "react";
import { AppContext } from "../App";

export function ControlBar_1(props) {
  return (
    <div className="sidebar">
      <Btn />
      <div className="delimeter"></div>
      <Btn1 />
      <Btn2 />
    </div>
  )
}

function Btn(props) {
  const { state, dispatch } = useContext(AppContext);

  return (
    <div className="widget">
      <div onClick={() => { alert('Переход на этап 2') }} className="btn">Отправить в компанию</div>
    </div>
  )
}

function Btn1(props) {
  const { state, dispatch } = useContext(AppContext);
  const [menu, openMenu] = useState(false);

  const toggleMenu = () => {
    openMenu(!menu);
  }

  const change = (n) => {
    dispatch({
      type: 'updateOption',
      payload: {
        key: 'walletCount',
        value: state.walletCount + n
      }
    })
  }

  return (
    <div className="widget">
      <div onClick={toggleMenu} className="btn has-submenu">
        {!menu ? 'Кол-во объявлений' : 'Закрыть'}
        {!menu && <span className="badge">{state.walletCount}</span>}</div>
      {menu &&
        <div className="sidebar-submenu">
          <div onClick={() => change(1)} className="btn icon"><i className="bi bi-plus"></i></div>
          <span className="wallet-count" style={{ fontSize: '1.5rem', margin: '5px', display: 'block' }}>{state.walletCount}</span>
          <div onClick={() => change(-1)} className="btn icon"><i className="bi bi-dash"></i></div>
        </div>
      }
    </div>
  )
}

function Btn2(props) {
  const { state, dispatch } = useContext(AppContext);
  const [menu, openMenu] = useState(false);
  const [submenu, openSubmenu] = useState(false);

  const toggleMenu = () => {
    if (menu) {
      openSubmenu(false)
    }
    openMenu(!menu);
  }

  const change = (n) => {
    dispatch({
      type: 'updateOption',
      payload: {
        key: 'method',
        value: n
      }
    })
    toggleMenu();
  }

  return (
    <div className="widget">
      <div onClick={toggleMenu} className="btn has-submenu">
        {!menu ? 'Метод сборки' : 'Закрыть'}
        {!menu && <span className="badge">{state.method}</span>}</div>
      {menu &&
        <div className="sidebar-submenu">
          <div
            onClick={() => openSubmenu('STAG')}
            style={{ fontSize: '1rem', marginBottom: '10px' }}
            className={`btn ${submenu === 'STAG' && 'changed'}`}>STAG</div>
          {submenu === 'STAG' &&
            <div className="sidebar-submenu">
              <div onClick={() => change('STAG <20')} style={{ fontSize: '1rem', marginBottom: '10px', width: '100%' }} className="btn">{'<20'}</div>
              <div onClick={() => change('STAG 20+')} style={{ fontSize: '1rem', marginBottom: '10px', width: '100%' }} className="btn">20+</div>
              <div onClick={() => change('STAG 100+')} style={{ fontSize: '1rem', marginBottom: '10px', width: '100%' }} className="btn">100+</div>
              <div onClick={() => change('STAG 500+')} style={{ fontSize: '1rem', marginBottom: '10px', width: '100%' }} className="btn">500+</div>
              <div onClick={() => change('STAG 1000+')} style={{ fontSize: '1rem', width: '100%' }} className="btn">1000+</div>
            </div>
          }
          <div
            onClick={() => openSubmenu('SKAG')}
            style={{ fontSize: '1rem', marginBottom: '10px' }}
            className={`btn ${submenu === 'SKAG' && 'changed'}`}>SKAG</div>
          {submenu === 'SKAG' &&
            <div className="sidebar-submenu">
              <div onClick={() => change('SKAG 10')} style={{ fontSize: '1rem', marginBottom: '10px', width: '45px' }} className="btn">10</div>
              <div onClick={() => change('SKAG 15')} style={{ fontSize: '1rem', marginBottom: '10px', width: '45px' }} className="btn">15</div>
              <div onClick={() => change('SKAG 20')} style={{ fontSize: '1rem', width: '45px' }} className="btn">20</div>
            </div>
          }
        </div>
      }
    </div>
  )
}