class TableClass {
    constructor(n) {
        this.colsCount = n;
        this.rows = [];
    }

    setCell(x, y, data) {
        while (this.rows.length <= y) {
            this.rows.push(this.newRow());
        }
        if (this.rows[y] !== undefined && this.rows[y][x] !== undefined) {
            this.rows[+y][+x].data = data;
        }
    }

    setCol(x, data) {
        for (let i = 0; i < data.length; i++) {
            this.setCell(x, i, data[i]);
        };
        return this.rows;
    }

    getRow(y) {
        return this.rows[y];
    }

    toggleRowDisable(y, value) {
        this.rows[y].forEach((cell) => {
            cell.options.disabled = !value;
        })
        return this.rows;
    }

    newCell() {
        return {
            data: '',
            options: {
                disabled: false
            }
        }
    }

    newRow() {
        let r = [];
        for (let n = 0; n < this.colsCount; n++) {
            r.push(this.newCell());
        }
        return r;
    }

    init() {
        // add rows to table
        for (let i = 0; i < 10; i++) {
            this.rows.push(this.newRow());
        }

        return this;
    }
}

export default TableClass;